@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Technology {
  background-color: #F0F0F0;
  padding: 6px 12px;
  border-radius: 10px;
  margin: 0 3px;
  cursor: default;
  vertical-align: baseline;
  white-space: nowrap;
  text-overflow: ellipsis;

  @media (max-width: $medium-size) {
    padding: 4px 8px;
    margin: 0 2px;
  }

  &:hover {
    background-color: $yellow-color;
  }

  &Tooltip {
    background-color: #FFFFFF;
    padding: 40px;
    border-radius: 20px;

    &Arrow {

    }
  }
}
