@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.Navigation {
  padding-top: 100px;
  padding-left: 0;

  @media (max-width: $medium-size) {
    display: none;

    &Visible {
      padding: 15px 10vw;
      left: 0;
      top: 0;
      display: flex;
      width: 80vw;
    }
  }

  &Button {
    display: none;

    @media (max-width: $medium-size) {
      display: block;
      padding: 20px;
    }
  }

}
